import React, { useState } from 'react';
import { Container, Row, Col, Form, Button, Alert } from 'react-bootstrap';

const NewsletterSection: React.FC = () => {
  const [email, setEmail] = useState('');
  const [showAlert, setShowAlert] = useState(false);
  const [alertMessage, setAlertMessage] = useState('');
  const [alertVariant, setAlertVariant] = useState<'success' | 'danger'>('success');

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!email) {
      setAlertMessage('Prosz poda adres email');
      setAlertVariant('danger');
      setShowAlert(true);
      return;
    }

    if (!/\S+@\S+\.\S+/.test(email)) {
      setAlertMessage('Prosz poda prawidowy adres email');
      setAlertVariant('danger');
      setShowAlert(true);
      return;
    }

    // Symulacja zapisu do newslettera
    setAlertMessage('Dzikujemy! Zostae zapisany do naszego newslettera.');
    setAlertVariant('success');
    setShowAlert(true);
    setEmail('');

    // Ukryj alert po 5 sekundach
    setTimeout(() => {
      setShowAlert(false);
    }, 5000);
  };

  return (
    <section className="newsletter-section py-5">
      <Container>
        <Row>
          <Col lg={8} className="mx-auto text-center">
            <div className="newsletter-content">
              <i className="bi bi-envelope-heart text-white mb-3" style={{ fontSize: '3rem' }}></i>
              <h2 className="text-white mb-3">Docz do naszej spoecznoci!</h2>
              <p className="text-white mb-4">
                Otrzymuj najnowsze artykuy, porady dla wacicieli psw i ekskluzywne oferty 
                prosto do swojej skrzynki mailowej. Zapisz si do naszego newslettera ju dzi!
              </p>
              
              {showAlert && (
                <Alert 
                  variant={alertVariant} 
                  className="mb-4"
                  onClose={() => setShowAlert(false)} 
                  dismissible
                >
                  {alertMessage}
                </Alert>
              )}

              <Form onSubmit={handleSubmit} className="newsletter-form">
                <Row className="g-2 justify-content-center">
                  <Col md={7} lg={6}>
                    <Form.Control
                      type="email"
                      placeholder="Twj adres email"
                      value={email}
                      onChange={(e) => setEmail(e.target.value)}
                      size="lg"
                      className="newsletter-input"
                    />
                  </Col>
                  <Col md={5} lg={4}>
                    <Button 
                      type="submit" 
                      variant="light" 
                      size="lg" 
                      className="w-100 newsletter-btn"
                    >
                      <i className="bi bi-send me-2"></i>
                      Zapisz si
                    </Button>
                  </Col>
                </Row>
              </Form>
              
              <p className="text-white-50 mt-3 small">
                <i className="bi bi-shield-check me-1"></i>
                Nie wysyamy spamu. Moesz si wypisa w kadej chwili.
              </p>
            </div>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default NewsletterSection;
