import { useEffect } from 'react';
import { useLocation } from 'react-router-dom';

/**
 * Komponent automatycznie przewijajcy stron na gr przy zmianie route
 */
const ScrollToTop = () => {
  const { pathname } = useLocation();

  useEffect(() => {
    // Przewi na gr przy kadej zmianie cieki
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth' // Pynne przewijanie
    });
  }, [pathname]);

  return null;
};

export default ScrollToTop;
